.App {
  text-align: center;
}

.leftBox {
  background: #faebd7;

  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px;
}
.mainBox {
  display: flex;
  width: 100%;
  height: 100vh;
}

form {
  display: flex;
  flex-direction: column;
  width: 20rem;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 2%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 4rem;
  box-shadow: 0 2px 2px 2px rgba(0, 0, 0, 0.2);
  background: rgb(245, 163, 118);
  background: linear-gradient(
    309deg,
    rgba(245, 163, 118, 1) 5%,
    rgba(196, 138, 242, 1) 100%
  );
}

input {
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
}

select {
  width: 100%;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: none;
  outline: none;
}

input:hover {
}

.OutputArea {
  background-color: #6495ed;
  width: 100%;
  padding: 10px;
}
.InputArea {
  background: #faebd7;

  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
